.wave-button {
  position: relative;
  overflow: hidden;
  display: inline-block;
  z-index: 1;
  -webkit-tap-highlight-color: transparent;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
}
.wave-button__inner {
  position: absolute;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  margin-top: -5px;
  margin-left: -5px;
  pointer-events: none;
  opacity: 1;
  background-color: #fff;
  background-color: rgba(0,0,0,0.2);
  top: 100%;
  left: 100%;
  display: block;
  -webkit-transform: scale(1);
  transform: scale(1);
  box-shadow: 0 0 20px 0 rgba(0,0,0,0.3), inset 0px 0px 10px 0px rgba(0,0,0,0.25);
}
